// src/db.js
// Supabase + localStorage 
// : inspectProblemRows()  repairScores() /
// repairScores()  win=40, draw=20, loss=10 

import { createClient } from "@supabase/supabase-js";

const SUPABASE_URL = process.env.REACT_APP_SUPABASE_URL;
const SUPABASE_ANON_KEY = process.env.REACT_APP_SUPABASE_ANON_KEY;

let supabase = null;
if (SUPABASE_URL && SUPABASE_ANON_KEY) {
  supabase = createClient(SUPABASE_URL, SUPABASE_ANON_KEY);
}

const KEY = "janken_records_v1";

// ---- Normalization helpers (robust) ----
function normalizeHandToKey(raw) {
  if (raw === null || raw === undefined) return null;
  const s = String(raw).trim();
  if (!s) return null;
  if (["rock","scissors","paper"].includes(s)) return s;
  if (s.includes("") || s.includes("")) return "rock";
  if (s.includes("") || s.includes("")) return "scissors";
  if (s.includes("") || s.includes("") || s.includes("")) return "paper";
  const lower = s.replace(/\s+/g,'').toLowerCase();
  if (lower.includes("") || lower.includes("") || lower.includes("gu")) return "rock";
  if (lower.includes("") || lower.includes("") || lower.includes("cho")) return "scissors";
  if (lower.includes("") || lower.includes("") || lower.includes("pa")) return "paper";
  if (s === "0" || s === "1" || s === "2") {
    if (s === "0") return "rock";
    if (s === "1") return "scissors";
    if (s === "2") return "paper";
  }
  return null;
}

function normalizeResultToKey(raw) {
  if (raw === null || raw === undefined) return null;
  const s = String(raw).trim();
  if (!s) return null;
  if (["win","draw","loss"].includes(s)) return s;
  const lower = s.replace(/\s+/g,'').toLowerCase();
  if (lower === "" || lower === "" || lower.includes("win")) return "win";
  if (lower === "" || lower === "" || lower.includes("draw")) return "draw";
  if (lower === "" || lower === "" || lower.includes("loss")) return "loss";
  if (s === "W" || s === "w") return "win";
  if (s === "D" || s === "d") return "draw";
  if (s === "L" || s === "l") return "loss";
  return null;
}

function normalizeRecordLoaded(r) {
  if (!r) return null;
  const handKey = normalizeHandToKey(r.hand);
  const resultKey = normalizeResultToKey(r.result);
  return {
    id: r.id != null ? String(r.id) : Date.now().toString(),
    date: r.date ? String(r.date).slice(0,10) : (r.created_at ? String(r.created_at).slice(0,10) : new Date().toISOString().slice(0,10)),
    hand: handKey || null,
    raw_hand: (r.hand != null ? String(r.hand) : null),
    result: resultKey || null,
    raw_result: (r.result != null ? String(r.result) : null),
    score: Number.isFinite(Number(r.score)) ? Number(r.score) : 0,
    created_at: r.created_at || null
  };
}

function normalizeRecordForUpsert(r) {
  return {
    id: String(r.id),
    date: r.date,
    hand: normalizeHandToKey(r.hand) || null,
    result: normalizeResultToKey(r.result) || null,
    score: Number.isFinite(Number(r.score)) ? Number(r.score) : 0,
    created_at: r.created_at || null
  };
}

// ---- Core functions ----
export async function loadRecords() {
  if (supabase) {
    const { data, error } = await supabase.from("records").select("*").order("date", { ascending: true });
    if (error) {
      console.warn("Supabase loadRecords error:", error);
    } else if (Array.isArray(data)) {
      return data.map(normalizeRecordLoaded);
    }
  }
  try {
    const raw = localStorage.getItem(KEY);
    if (!raw) return [];
    const arr = JSON.parse(raw);
    return Array.isArray(arr) ? arr.map(normalizeRecordLoaded) : [];
  } catch (e) {
    console.warn("loadRecords fallback error", e);
    return [];
  }
}

export async function saveRecord(record) {
  const rec = normalizeRecordForUpsert(record);
  if (supabase) {
    const { error } = await supabase.from("records").upsert(rec);
    if (error) {
      console.warn("Supabase saveRecord error:", error);
    } else {
      return true;
    }
  }
  try {
    const all = await loadRecords();
    all.push(normalizeRecordLoaded(rec));
    localStorage.setItem(KEY, JSON.stringify(all));
    return true;
  } catch (e) {
    console.warn("saveRecord fallback error", e);
    return false;
  }
}

export async function deleteRecord(id) {
  if (supabase) {
    const { error } = await supabase.from("records").delete().eq("id", id);
    if (error) {
      console.warn("Supabase deleteRecord error:", error);
    } else {
      return true;
    }
  }
  try {
    const all = (await loadRecords()).filter(r => r.id !== id);
    localStorage.setItem(KEY, JSON.stringify(all));
    return true;
  } catch (e) {
    console.warn("deleteRecord fallback error", e);
    return false;
  }
}

// migrate local -> supabase
export async function migrateLocalToSupabase() {
  if (!supabase) throw new Error("Supabase not configured");
  const raw = localStorage.getItem(KEY);
  if (!raw) return { migrated: 0 };
  const arr = JSON.parse(raw) || [];
  if (!Array.isArray(arr) || arr.length === 0) return { migrated: 0 };
  const toInsert = arr.map(normalizeRecordForUpsert);
  const { error } = await supabase.from("records").upsert(toInsert);
  if (error) throw error;
  return { migrated: toInsert.length };
}

// --- inspect problem rows (score=0 or missing normalized hand/result) ---
export async function inspectProblemRows(limit = 200) {
  const problems = [];
  if (supabase) {
    const { data, error } = await supabase
      .from("records")
      .select("*")
      .or("score.eq.0,hand.is.null,result.is.null")
      .limit(limit);
    if (error) {
      console.warn("inspectProblemRows supabase error:", error);
    } else {
      data.forEach(d => problems.push(normalizeRecordLoaded(d)));
    }
  } else {
    const arr = (await loadRecords()) || [];
    arr.forEach(d => {
      if (!d.hand || !d.result || (d.score === 0)) problems.push(d);
    });
  }
  return problems;
}

// --- repair scores (set score from result when score is 0 or null) ---
// NOTE: using win=40 per request
export async function repairScores() {
  if (!supabase) {
    // localStorage repair
    const arr = (await loadRecords()) || [];
    let repaired = 0;
    const out = arr.map(r => {
      const rr = {...r};
      if (!Number.isFinite(rr.score) || rr.score === 0) {
        if (rr.result === 'win') { rr.score = 40; repaired++; }
        else if (rr.result === 'draw') { rr.score = 20; repaired++; }
        else if (rr.result === 'loss') { rr.score = 10; repaired++; }
      }
      return rr;
    });
    localStorage.setItem(KEY, JSON.stringify(out));
    return { repaired };
  }

  // supabase: fetch candidate rows then upsert with corrected scores
  const { data, error } = await supabase
    .from("records")
    .select("*")
    .or("score.eq.0,score.is.null")
    .limit(1000);

  if (error) throw error;
  if (!Array.isArray(data) || data.length === 0) return { repaired: 0 };

  const toUpsert = data.map(d => {
    const normalized = normalizeRecordForUpsert(d);
    if (!normalized.score || normalized.score === 0) {
      if (normalized.result === 'win') normalized.score = 40;
      else if (normalized.result === 'draw') normalized.score = 20;
      else if (normalized.result === 'loss') normalized.score = 10;
      else normalized.score = 0; // if result missing, leave 0
    }
    return normalized;
  });

  const { error: upsertError } = await supabase.from("records").upsert(toUpsert, { onConflict: ["id"] });
  if (upsertError) throw upsertError;
  return { repaired: toUpsert.filter(r=>r.score>0).length };
}

// --- normalize hand/result in supabase rows (upsert normalized) ---
export async function normalizeSupabaseRecords() {
  if (!supabase) throw new Error("Supabase not configured");
  const { data, error } = await supabase.from("records").select("*").limit(5000);
  if (error) throw error;
  if (!Array.isArray(data) || data.length === 0) return { normalized: 0 };

  const normalized = data.map(d => {
    const upd = normalizeRecordForUpsert(d);
    if (d.created_at) upd.created_at = d.created_at;
    return upd;
  });

  const { error: upsertError } = await supabase.from("records").upsert(normalized, { onConflict: ["id"] });
  if (upsertError) throw upsertError;
  return { normalized: normalized.length };
}
