// src/algorithms.js
// : hand emoji / 

// :  hand  'rock' | 'scissors' | 'paper' 
function normalizeHand(raw) {
  if (!raw && raw !== 0) return null;
  const s = String(raw).trim();
  // 
  if (["rock","scissors","paper"].includes(s)) return s;
  // Emoji 
  if (s.includes("") || s.includes("") || s.includes("") || s.includes("")) {
    if (s.includes("")) return "rock";
    if (s.includes("")) return "scissors";
    if (s.includes("") || s.includes("")) return "paper";
  }
  // 
  const lower = s.toLowerCase();
  if (lower.includes("") || lower.includes("") || lower.includes("") || lower.includes("gu")) return "rock";
  if (lower.includes("") || lower.includes("") || lower.includes("cho")) return "scissors";
  if (lower.includes("") || lower.includes("") || lower.includes("pa")) return "paper";

  // fallback: null ()
  return null;
}

export function computeWeightedStats(records = [], today = new Date(), recencySensitivity = 0.4) {
  const hands = ["rock","scissors","paper"];
  const stats = {
    rock:{plays:0,wins:0,draws:0,losses:0,scoreSum:0,avgScore:0,winRate:0},
    scissors:{plays:0,wins:0,draws:0,losses:0,scoreSum:0,avgScore:0,winRate:0},
    paper:{plays:0,wins:0,draws:0,losses:0,scoreSum:0,avgScore:0,winRate:0}
  };

  const lambda = recencySensitivity * 0.2;

  if (!Array.isArray(records)) return stats;

  records.forEach(r => {
    const normalized = normalizeHand(r.hand);
    if (!normalized) return; //  hand 

    const age = (new Date(today) - new Date(r.date)) / (1000*60*60*24);
    const w = Math.exp(-lambda * Math.max(0, age));

    const st = stats[normalized];
    if (!st) return; // 

    st.plays += w;
    if (r.result === "win" || r.result === "") st.wins += w;
    if (r.result === "draw" || r.result === "") st.draws += w;
    if (r.result === "loss" || r.result === "") st.losses += w;
    st.scoreSum += w * (Number(r.score) || 0);
  });

  hands.forEach(h => {
    const s = stats[h];
    s.avgScore = s.plays ? s.scoreSum / s.plays : 0;
    s.winRate = s.plays ? s.wins / s.plays : 0;
  });

  return stats;
}

export function recommendHand(stats = {}, expectedVsWinRate = 0.5) {
  const hands = ["rock","scissors","paper"];
  const avgScores = hands.map(h => (stats[h] && stats[h].avgScore) ? stats[h].avgScore : 0);
  const minS = Math.min(...avgScores);
  const maxS = Math.max(...avgScores);
  const scores = {};

  hands.forEach(h => {
    const s = stats[h] || {avgScore:0, winRate:0};
    const normScore = (maxS === minS) ? 0 : (s.avgScore - minS) / (maxS - minS);
    scores[h] = expectedVsWinRate * normScore + (1 - expectedVsWinRate) * (s.winRate || 0);
  });

  // 
  let best = hands[0], bestv = scores[best];
  hands.forEach(h => {
    if (scores[h] > bestv) { best = h; bestv = scores[h]; }
  });
  return best;
}
