import React from "react";
import { Line } from "react-chartjs-2";
import 'chart.js/auto';

function Stats({records}) {
  // Group by date -> compute avg score
  const grouped = {};
  records.forEach(r=>{
    grouped[r.date] = grouped[r.date] || {sum:0, count:0};
    grouped[r.date].sum += r.score;
    grouped[r.date].count += 1;
  });
  const dates = Object.keys(grouped).sort();
  const avg = dates.map(d => Math.round(grouped[d].sum / grouped[d].count));
  const data = {
    labels: dates,
    datasets: [
      { label: "", data: avg, tension: 0.2, fill:false, borderWidth:2 },
      { label: "", data: dates.map(()=>20), borderDash: [6,4], borderColor: '#d9534f', fill:false }
    ]
  };
  return (
    <div style={{padding:20}}>
      <h2></h2>
      <Line data={data} />
    </div>
  );
}

export default Stats;
