// src/App.js
import React, { useEffect, useState } from "react";
import "./App.css";
import { computeWeightedStats, recommendHand } from "./algorithms";
import { loadRecords, saveRecord, deleteRecord } from "./db";
import Stats from "./components/Stats";

const HANDS = [
  { id: "rock", emoji: "", label: "" },
  { id: "scissors", emoji: "", label: "" },
  { id: "paper", emoji: "", label: "" },
];

function App() {
  const [tab, setTab] = useState("input");
  const [records, setRecords] = useState([]);
  const [date, setDate] = useState(new Date().toISOString().slice(0, 10));
  const [selectedHand, setSelectedHand] = useState(null);
  const [result, setResult] = useState("draw");
  const [recencySensitivity, setRecencySensitivity] = useState(0.4);
  const [expectedVsWinRate, setExpectedVsWinRate] = useState(0.5);
  const [saving, setSaving] = useState(false);

  useEffect(() => {
    (async () => {
      try {
        const rs = await loadRecords();
        console.log("loadRecords ->", rs && rs.length);
        setRecords(rs || []);
      } catch (e) {
        console.error("loadRecords failed", e);
        setRecords([]);
      }
    })();
  }, []);

  // make recommended hand selected by default so user can just press save
  useEffect(() => {
    const stats = computeWeightedStats(records, new Date(date), recencySensitivity);
    const recommended = recommendHand(stats, expectedVsWinRate);
    if (!selectedHand) setSelectedHand(recommended);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [records]);

  const validScores = (records || []).map(r => Number(r.score)).filter(v => Number.isFinite(v));
  const averageScore = validScores.length
    ? (validScores.reduce((a, b) => a + b, 0) / validScores.length).toFixed(2)
    : "0.00";

  const stats = computeWeightedStats(records, new Date(date), recencySensitivity);
  const recommendedHand = recommendHand(stats, expectedVsWinRate);
  const finalScore = Number.isFinite(stats[recommendedHand]?.avgScore)
    ? Math.round(stats[recommendedHand].avgScore)
    : 0;

  const refresh = async () => {
    try {
      const rs = await loadRecords();
      console.log("refresh ->", rs && rs.length);
      setRecords(rs || []);
    } catch (e) {
      console.error("refresh failed", e);
    }
  };

  // Robust onSave with logging, optimistic UI update, and error handling
  const onSave = async () => {
    console.log("onSave clicked", { selectedHand, result, date, saving });
    if (saving) {
      console.log("already saving, ignore click");
      return;
    }
    if (!selectedHand) {
      alert("");
      return;
    }

    setSaving(true);
    const score = result === "win" ? 40 : result === "draw" ? 20 : 10;
    const record = {
      id: Date.now().toString(),
      date,
      hand: selectedHand,
      result,
      score,
    };

    // optimistic UI: add to records immediately so user sees change
    setRecords(prev => [...prev, record]);

    try {
      const ok = await saveRecord(record);
      console.log("saveRecord returned:", ok);
      if (ok === false) {
        throw new Error("saveRecord returned false");
      }
      // ensure fresh state from backend
      await refresh();
      alert("");
    } catch (e) {
      console.error("saveRecord failed:", e);
      // rollback optimistic update
      setRecords(prev => prev.filter(r => r.id !== record.id));
      alert("");
    } finally {
      setSaving(false);
    }
  };

  // debug helper to show last 5 records in console
  const logState = () => {
    console.log("STATE: selectedHand:", selectedHand, "result:", result, "recordsCount:", records.length);
    console.log("LAST 5 records:", records.slice(-5));
  };

  return (
    <div className="app">
      <header className="header">
        <h1></h1>
        <div className="tabs">
          <button className={tab === "input" ? "active" : ""} onClick={() => setTab("input")}></button>
          <button className={tab === "stats" ? "active" : ""} onClick={() => setTab("stats")}></button>
        </div>
      </header>

      <main className="container">
        <div className="avg-block">
          <div className="label"></div>
          <div className="avg-value">{averageScore}</div>
        </div>

        <div style={{ marginTop: 12 }}>
          <button onClick={logState} style={{ fontSize: 12 }}>DEBUG: log state</button>
        </div>

        <div className="slider-row">
          <label>Recency sensitivity</label>
          <input type="range" min="0" max="1" step="0.01"
            value={recencySensitivity}
            onChange={e => setRecencySensitivity(Number(e.target.value))}
          />
        </div>

        <div className="slider-row">
          <label>Expected vs WinRate</label>
          <input type="range" min="0" max="1" step="0.01"
            value={expectedVsWinRate}
            onChange={e => setExpectedVsWinRate(Number(e.target.value))}
          />
        </div>

        {tab === "input" && (
          <>
            <div className="date-row">
              <label></label>
              <input type="date" value={date} onChange={e => setDate(e.target.value)} />
            </div>

            <div className="recommendation">
              <div className="final">Recommendation FinalScore: {finalScore}</div>

              <div className="cards">
                {HANDS.map(h => (
                  <div
                    key={h.id}
                    className={`card ${selectedHand === h.id ? "highlight" : ""}`}
                    onClick={() => {
                      console.log("card clicked:", h.id);
                      setSelectedHand(h.id);
                    }}
                    role="button"
                    tabIndex={0}
                    onKeyDown={(e) => { if (e.key === "Enter") setSelectedHand(h.id); }}
                  >
                    <div className="emoji">{h.emoji}</div>
                    <div className="label">{h.label}</div>
                  </div>
                ))}
              </div>
            </div>

            <div className="result-buttons" style={{ marginTop: 12 }}>
              <div className={`pill ${result === "win" ? "selected" : ""}`} onClick={() => { console.log("result->win"); setResult("win"); }}></div>
              <div className={`pill ${result === "draw" ? "selected" : ""}`} onClick={() => { console.log("result->draw"); setResult("draw"); }}></div>
              <div className={`pill ${result === "loss" ? "selected" : ""}`} onClick={() => { console.log("result->loss"); setResult("loss"); }}></div>
            </div>

            <div className="actions" style={{ marginTop: 12 }}>
              <button className="primary" onClick={onSave} disabled={saving}>
                {saving ? "" : ""}
              </button>
            </div>
          </>
        )}

        {tab === "stats" && <Stats records={records} />}
      </main>
    </div>
  );
}

export default App;
